'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name AsyncIteratorSymbol
* @memberof ns
* @readonly
* @type {(symbol|null)}
* @see {@link module:@stdlib/symbol/async-iterator}
*/
import AsyncIteratorSymbol from './../async-iterator';
setReadOnly( ns, 'AsyncIteratorSymbol', AsyncIteratorSymbol );

/**
* @name Symbol
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/symbol/ctor}
*/
import Symbol from './../ctor';
setReadOnly( ns, 'Symbol', Symbol );

/**
* @name HasInstanceSymbol
* @memberof ns
* @readonly
* @type {(symbol|null)}
* @see {@link module:@stdlib/symbol/has-instance}
*/
import HasInstanceSymbol from './../has-instance';
setReadOnly( ns, 'HasInstanceSymbol', HasInstanceSymbol );

/**
* @name IsConcatSpreadableSymbol
* @memberof ns
* @readonly
* @type {(symbol|null)}
* @see {@link module:@stdlib/symbol/is-concat-spreadable}
*/
import IsConcatSpreadableSymbol from './../is-concat-spreadable';
setReadOnly( ns, 'IsConcatSpreadableSymbol', IsConcatSpreadableSymbol );

/**
* @name IteratorSymbol
* @memberof ns
* @readonly
* @type {(symbol|null)}
* @see {@link module:@stdlib/symbol/iterator}
*/
import IteratorSymbol from './../iterator';
setReadOnly( ns, 'IteratorSymbol', IteratorSymbol );

/**
* @name ReplaceSymbol
* @memberof ns
* @readonly
* @type {(symbol|null)}
* @see {@link module:@stdlib/symbol/replace}
*/
import ReplaceSymbol from './../replace';
setReadOnly( ns, 'ReplaceSymbol', ReplaceSymbol );

/**
* @name ToPrimitiveSymbol
* @memberof ns
* @readonly
* @type {(symbol|null)}
* @see {@link module:@stdlib/symbol/to-primitive}
*/
import ToPrimitiveSymbol from './../to-primitive';
setReadOnly( ns, 'ToPrimitiveSymbol', ToPrimitiveSymbol );


// EXPORTS //

export default ns;
